<template>
  <div id="recent-articles-wrapper" class="py-5">
    <h2 class="mb-5 coloured-underline">Recent Articles</h2>
    <b-container id="recent-articles-container">
      <b-row>
        <b-col v-for="article in data" :key="article._path" class="mb-3" cols="12" lg="4" sm="6">
          <content-renderer :value="article">
            <b-card class="article-summary-card" :title="article.title" tag="article" no-body>
              <img class="card-img" :src="article.img" :img-alt="article.title" />
              <b-card-body>
                <b-link :to="article._path">
                  <h4 class="card-title">
                    {{ article.title }}
                  </h4>
                </b-link>
                <b-card-text>
                  <div v-html="article.summary"></div>
                </b-card-text>
                <b-button :to="article._path" variant="success">Read More</b-button>
              </b-card-body>
            </b-card>
          </content-renderer>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script setup>
const date = new Date();
const { data, error } = await useAsyncData("blog", async () => {
  const content = await queryContent("blog")
    .where({
      date: { $exists: true, $lte: date },
      draft: { $not: true },
      hidden: { $not: true },
    })
    .sort({ date: -1 })
    // .limit(3)
    .find();

  return content;
});

if (error.value) {
  console.error(error.value);
}
</script>

